import React from "react";
import "./UserTransactionDataThumbnail_Main.css";
import clockIcon from "../../images/clock.png";

function UserTransactionDataThumbnail_Main(props) {
	const { theme, selectedTheme } = props;
	const addTagLineSpecificToURL = {
		"/component-test": {
			tagline: "Vaibhav has given RS XXX to USER",
			amountSummary:
				"This is the total amount of money Vaibhav have given to USER ",
		},
		"/money-to-take": {
			tagline: "Vaibhav has to take RS XXX from USER",
			amountSummary:
				"This is the total amount of money Vaibhav have to take from USER ",
		},
	};
	console.log();
	return (
		<div
			className="user-transaction-thumbnail-container px-2 py-4"
			style={{
				background:
					selectedTheme == "light" ? theme.light.color : theme.dark.color,
				boxShadow:
					selectedTheme == "light"
						? theme.light.boxShadow
						: theme.dark.boxShadow,
			}}
		>
			<div className="row d-flex justify-content-center m-0  ">
				<div className="col-xl-2 col-lg-2 col-md-2 p-0 d-flex justify-content-center">
					<img
						src="https://cdn.pixabay.com/photo/2017/07/18/23/23/user-2517433_960_720.png"
						className="img-fluid  user-transaction-data-thumnail__image"
					/>
				</div>
				<div
					style={{
						color:
							selectedTheme == "light" ? theme.dark.color : theme.light.color,
					}}
					className="col-xl-6 col-lg-6 col-md-6 d-flex flex-column align-items-start justify-content-between"
				>
					<h1 className="user-transaction-data-thumnail__name  m-0">Vaibhav</h1>
					<div className="user-transaction-data-thumnail__date mt-1 px-2 py-1 d-flex justify-content-center align-items-center">
						<img
							src={clockIcon}
							className="img-fluid mr-2 user-transaction-data-thumnail__date-clock-icon"
							alt="Clock icon"
						/>
						<h6 className="user-transaction-data-thumnail__date-text text-white  m-0">
							23-04-2020
						</h6>
					</div>

					<strong
						style={{
							color:
								selectedTheme == "light" ? theme.dark.color : theme.light.color,
						}}
						className=" font-weight-bold user-transaction-data-thumnail__tagline font-italic my-3"
					>
						{addTagLineSpecificToURL[window.location.pathname]["tagline"]}
					</strong>
					<p
						className="user-transaction-data-thumnail__description"
						style={{
							color:
								selectedTheme == "light" ? theme.dark.color : theme.light.color,
						}}
					>
						I'm a little confused why we keep making class components...
					</p>
				</div>
				<div
					className="col-xl-4 col-lg-4 col-md-4 "
					style={{
						color:
							selectedTheme == "light" ? theme.dark.color : theme.light.color,
					}}
				>
					<h1 className="user-transaction-data-thumnail__amount font-weight-bold text-center mt-5">
						RS XXX
					</h1>
					<p className="user-transaction-data-thumnail__amount-summary  text-center">
						{addTagLineSpecificToURL[window.location.pathname]["amountSummary"]}
					</p>
				</div>
			</div>
			<div className="user-transaction-data-thumnail__action-btns mt-3 w-100 d-flex justify-content-between align-items">
				<div className="action-btns__CRUD d-flex justify-content-between align-items">
					<button className="action-btn__notify text-white mx-3 transaction-thumbnail-action-btn">
						Notify USER
					</button>
					<button className="action-btn__delete mr-3 text-white transaction-thumbnail-action-btn">
						Delete Transaction
					</button>
					<button className="action-btn__settle mr-3 text-white transaction-thumbnail-action-btn">
						Settle Transaction
					</button>
				</div>
				<div className="action-btns__load-history">
					<button className="action-btn__history mr-3 text-white transaction-thumbnail-action-btn">
						Load History
					</button>
				</div>
			</div>
		</div>
	);
}

export default UserTransactionDataThumbnail_Main;



